.Program {
	border: 4px solid;
	display: none;
	/* visibility: hidden; */
	/* height: ; */
	border-right: none;
	border-color: rgb(226, 223, 28) #e60707 rgb(241, 30, 213) rgb(20, 3, 51);
	padding: 10px;
	padding-top: 0;
	margin: auto;
	transition: .5s;
}
.ActPh{
	background:rgb(222, 240, 226) !important;
	color:red;
}
.ActProg{
	display: block;
	/* visibility: visible; */
}
.content {
	margin: auto;
	padding: 15px 0;
}
.top{
	text-align: center;
}
.top h2 {
	text-shadow: 0 0 5px rgb(110, 107, 107);
	background-color: rgb(206, 200, 116);
	margin-top: 20px;
	font-size: 16px;
	color: #8b0606;
	padding: 10px;
	border-radius: 15px;
}
@media (max-width:700px){
	.Program{
		width: 90%;
	}
	.h1 span{
		font-size: 13px;
		letter-spacing: 2px;
	}
	.h1 h1{
		font-size: 1rem !important;
	}
	.h1{
		width: 98%;
	}
	h1 i{
		display: none;
	}
	input[type=button] {
		margin-bottom: 5px;
	}
	.dataC{
		font-size: 13px;
		width: 96%;
	}
}
@media (min-width:700px){
	.dataC {
		font-size: 20px;
		width: 80%;
	}
	
	input[type=button] {
		font-size: 20px;
	}
	.h1{
		width: 70%;
		margin: auto;
		display: flex;
	}
	.h1:hover{
		background: rgb(165, 133, 216);
	}
	.Program{
		width: 65%;
	}
}
.h1 {
	border: 2px solid rgb(40, 7, 226);
	border-radius: 15px;
	justify-content: space-between;
	border-top-right-radius: 10px;
	padding: 6px;
	font-size: 13px;
	margin-top: 35px;
	box-shadow: 0 0 10px 0 red;
	cursor: pointer;
	background-color: rgb(251, 253, 251);
	transition: .3s;
}
.h1 h1 a{
	text-decoration: underline;
	color: red;
}
.h1 h1{
	letter-spacing: 1px;
	text-transform: capitalize;
	font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}


.h1:active {
	background: #81ffb1;
	color: rgb(197, 3, 3)
}

.h1:focus {
	width: 83%;
}

.date {
	color: #2807e6;
	width: 180px;
	font-size: 20px;	
	font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.dataC {
	color: lightgreen;
	/* display: none; */
	background: rgb(27, 31, 40);
	padding: 10px;
	user-select: all;
	height: 275px;
	overflow: auto;
	font-family: 'Courier New', Courier, monospace;
	border: 5px solid rgb(209, 25, 194);
	border-radius: 20px 6px 6px 13px;
}

input[type=button] {
	background-color: rgb(12, 238, 61);
	border: 2px dashed rgb(13, 14, 13);
	color: rgb(17, 16, 16);
	/* font-size: 20px; */
	cursor: pointer;
	border-bottom-left-radius: 20px;
	padding: 5px 10px;
	transition: .5s;
}

input[type=button]:hover {
	background-color: rgb(212, 233, 20);
	border-bottom-left-radius: 20px;
	font-weight: bold;
	padding: 7px 12px;
}

#Scrol{
	display: none;
}
.Scrol_Top_Bottm .go_top,
.Scrol_Top_Bottm .go_bottm,
.fold_bt .fold {
	position: fixed;
	z-index: 1;
	padding: 10px;
	background-color: rgb(11, 3, 54);
	border: 0;
	box-shadow: 0 0 8px 0 black;
	outline: none;
	color: white;
	font-weight: bold;
	border-radius: 25px;
	cursor: pointer;
	text-decoration: none;
	font-size: 18px;
	border-bottom-left-radius: 5px;
	border-top-right-radius: 5px;
}

.Scrol_Top_Bottm .go_top:hover,
.Scrol_Top_Bottm .go_bottm:hover,
.fold_bt .fold:hover {
	color: black;
	background-color: rgb(8, 235, 235);
	box-shadow: 0 0 10px 0 red;
	border-radius: 25px;
	border-bottom-right-radius: 5px;
	border-top-left-radius: 5px;
}

.Scrol_Top_Bottm .go_top:active,
.Scrol_Top_Bottm .go_bottm:active,
.fold:active {
	background-color: rgb(45, 202, 39);
	color: red;
	transform: translateY(5px);
}

.Scrol_Top_Bottm .go_top {
	bottom: 50px;
	right: 15px;
}

.fold_bt .fold {
	bottom: 50px;
	/* display: none; */
	left: 15px;
}
.Actfold{
	display: none;
}
.Scrol_Top_Bottm .go_bottm {
	top: 160px;
	right: 5px;
}